import React, {useState, useEffect} from 'react'

//component
import Word from "./Word/Word"
import TWord from "./TWord/TWord"

//classes 
import classes from "./Area.css"

const Area = React.memo((props) => {

    return (
        <div className={classes.Area}>

            <div className={classes.Box}>
                {/* dropshadow */}
                
                {!props.TypingTestData.isStarted && 
                    (props.TypingTestData.isCompleted 
                    ?    
                    <div className={classes.dropOver}>
                        <div className={classes.top}>
                            <div className={classes.value}>
                                {props.TypingTestData.wpm}
                            </div>
                            <div className={classes.label}>
                                WPM
                            </div>
                        </div>
                         
                        <div className={classes.bottom} style={{opacity: (props.ReadyToRestart ? 1 : 0)}}>
                            Space to Restart Typing
                        </div>

                    </div>
                    : 
                    <div className={classes.dropOver2}>
                        <div className={classes.BeforeStart}>
                            Space to Start Typing
                        </div>
                        <div className={classes.BeforeHint}>
                            Timmer set on {props.TypingTestData.time} min
                        </div>
                        <div className={classes.BeforeHint}>
                            Timmer starts when you start typing
                        </div>
                    </div>)
                }
            
                {(props.TypingTestData.isStarted && props.CurrentWord) && 
                // typed Word 
                <React.Fragment>
                <div className={classes.Typed+" "+classes.words}>
                    {props.typedWordArray.map((ele, index)=>{
                        return <Word key={index} word={ele.word} typed isCorrect={!(ele.correct < ele.word.length)} />
                    })}
                    
                </div>

                {/* // typing word */}
                <div className={classes.Typing+" "+classes.words}>
                    <TWord currentWord={props.CurrentWord}/>
                </div>

                {/* // word to type */}
                <div className={classes.ToBeType+" "+classes.words}>
                    <div className={classes.drop}></div>
                    {props.PendingArray.length>0 && props.PendingArray.map((ele, index)=>{
                        return <Word key={index} tobetyped word={ele} absColor={"#D4D4D4"}/>
                    })}
                    
                </div>
                </React.Fragment>
                }   
            </div>
            
        </div>
    )
})

export default Area
