import React, { useState } from 'react'


//component
import back from "./util/back"

//classes
import classes from "./Button.css"

const Button = (props) => {

    // default is primary
    let style = {
        backgroundColor: "#1D1D1D",
        color: "#CCCCCC",
    }

    if(props.secondary){
        style.backgroundColor= "#2A2A2A"
    }
    else if(props.green){
        style.backgroundColor= "#34658B"
    }
    else if(props.red){
        style.backgroundColor= "#AA3636"
    }

    if(props.grey){
        style.color= "#A5A5A5"
    }
    else if(props.blue){
        style.color= "#5AB1F5"
    }

    const clickHandler = () => {
        props.buttonClicked(props.val)
    }

    return (
        <div onClick={clickHandler} style={{...style, fontSize: (props.blue || props.green) && "1.8rem"}} className={classes.Button}>
            {props.backspace ? back(20  ) :props.val}
        </div>
    )
}

export default Button
