import React from 'react'

//classes
import classes from "./Status.css"

const Status = (props) => {
    return (
        <div className={classes.Status}>
            <div className={classes.Top}>
                <InfoBox label={"WPM"} WPM value={props.TypingTestData.wpm === NaN ? "0.00" : props.TypingTestData.wpm}/>
                <InfoBox label={"Accuracy"} value={props.TypingTestData.acc}/>
                <InfoBox label={"CPM"} value={props.TypingTestData.cpm}/>
            </div>
            <div className={classes.Bottom}>
                <InfoBox label={"Correct"} value={props.TypingTestData.correctWord}/>
                <InfoBox label={"Wrong"} INC value={props.TypingTestData.incorrectWord}/>
            </div>
        </div>
    )
}

const InfoBox = (props) => {
    return (
        <div className={classes.Info}>
            <div className={classes.label}>
                {props.label}
            </div>
            <div className={classes.value} style={{color: (props.WPM ? "#4EB9EE" : (props.INC ? "#FF3471" : "#606060"))}}>
                {`${props.value}${props.label === "Accuracy"? "%" : ""}`}
            </div>
        </div>
    )
}


export default Status
