const ADD = "+";
const SUB = "-";
const MUL = "Ã—";
const DIV = "Ã·";

export function calculate(operationArray){
    //priority of operator
        //solve left and write with operation
            // return the resultat value to the calculate function 
    console.log(operationArray)
    // #DIV 
    if(isOperator(operationArray[0])){
        operationArray.shift()
    }
    let newResult = [...operationArray]
    let mainResult = 0;
    let solvedArray = [];
    if(operationArray.indexOf(DIV) !== -1){
        console.log("find")
        let mainIndex = newResult.indexOf(DIV)
        
        if(mainIndex+1 >= newResult.length){
            newResult.pop()
            return [...calculate(newResult), DIV]
        }else{
            mainResult = newResult[mainIndex-1]/newResult[mainIndex+1]
            newResult.splice(mainIndex-1, 3);
            newResult.splice(mainIndex-1, 0, mainResult);
            if(newResult.length === 1){
                return mainResult
            }
            return calculate(newResult)
        }
    }
    else if(operationArray.indexOf(MUL) !== -1){
        let mainIndex = newResult.indexOf(MUL)
        
        if(mainIndex+1 >= newResult.length){
            newResult.pop()
            return [...calculate(newResult), MUL]
        }else{
            mainResult = newResult[mainIndex-1]*newResult[mainIndex+1]
            newResult.splice(mainIndex-1, 3);
            newResult.splice(mainIndex-1, 0, mainResult);
            if(newResult.length === 1){
                return mainResult
            }
            return calculate(newResult)
        }
    }
    else if(operationArray.indexOf(ADD) !== -1){
        let mainIndex = newResult.indexOf(ADD)
        
        if(mainIndex+1 >= newResult.length){
            newResult.pop()
            return [...calculate(newResult), ADD]
        }else{
            mainResult = newResult[mainIndex-1]+newResult[mainIndex+1]
            newResult.splice(mainIndex-1, 3);
            newResult.splice(mainIndex-1, 0, mainResult);
            if(newResult.length === 1){
                return mainResult
            }
            return calculate(newResult)
        }
    }
    else if(operationArray.indexOf(SUB) !== -1){
        let mainIndex = newResult.indexOf(SUB)
        
        if(mainIndex+1 >= newResult.length){
            newResult.pop()
            return [...calculate(newResult), SUB]
        }else{
            mainResult = newResult[mainIndex-1]-newResult[mainIndex+1]
            newResult.splice(mainIndex-1, 3);
            newResult.splice(mainIndex-1, 0, mainResult);
            if(newResult.length === 1){
                return mainResult
            }
            return calculate(newResult)
        }
    }
    else{
        return operationArray[0]
    }

}

function basicOperation(numA, numB, operation){
    switch (operation){
        case ADD:
            return numA+numB;
        case SUB:
            return numA-numB;
        case DIV:
            return numA/numB;
        case MUL:
            return numA*numB;        
    }
}

export function isOperator(value){
    switch(value){
        case ADD:
            return true;
        case SUB:
            return true;
        case DIV:
            return true;
        case MUL:
            return true;
        default:
            return false;
    }
}