import React from 'react'

//component
import Char from "./Char/Char"

//classes
import classes from "./TWord.css"

const TWord = (props) => {

    // console.log(props.currentWord.word)
    // console.log(props.currentWord)

    return (
        <div className={classes.TWord}>
            {Array.from(props.currentWord.word).map((ele, index)=>{
                // console.log(ele === props.currentWord.typed[index])
                return <Char key={index} isCorrect={ele === props.currentWord.typed[index]} onCursor={index === props.currentWord.cursor} untouched={!(props.currentWord.cursor > index)} char={ele}/>
            })}
        </div>
    )
}

export default TWord
