import React, {useState, useEffect} from 'react'

//component
import Timer from "./Timer/Timer"
import Area from "./Area/Area"
import Status from "./Status/Status"
import Word from "./WordData"
import TimeB from "./TimeB/TimeB"

//classes
import classes from "./Typing.css"

//Global Word Array



const Typing = (props) => {

    const [Time, setTime] = useState({
        isStarted: false,
        time: 0,
    }) //Time in minutes
    const [typingTestData, setTypingTestData] = useState() //time in minutes
    const [typedWordArray, setTypedWordArray] = useState([])
    const [pendingArray, setPendingArray] = useState([])
    const [currentWord, setCurrentWord] = useState(null)
    const [readyToRestart, setReadytoRestart] = useState(false)
    const [intervalId, setIntervalId] = useState(null)

    useEffect(()=>{
        
        setTypingTestData({
            time: 1,
            isStarted: false,
            correctWord: 0,
            incorrectWord: 0,
            wpm: 0,
            cpm: 0,
            acc: 100,
            isCompleted: false,
        })

    }, [])

    useEffect(()=>{

        window.addEventListener("keypress", keypressHandler)
        window.addEventListener("keydown", backspaceHandler)
        
        return ()=> {
            window.removeEventListener("keypress", keypressHandler)
            window.removeEventListener("keydown", backspaceHandler)
        }

    }, [typingTestData, currentWord, readyToRestart])

    useEffect(()=>{

        if(currentWord && typingTestData.isStarted)
        updateTypingData();

    }, [Time])

    const mixingWordArray = (array, no_of_shuffel, no_of_word) => {
        let newArray = [...array];
        //jumbling 2 words 2000 times randomly
        let randomIndex1 = Math.floor(Math.random()*newArray.length)
        let randomIndex2 = Math.floor(Math.random()*newArray.length)
        for(var i=0; i<no_of_shuffel; i++){
            let extra = newArray[randomIndex1] 
            newArray[randomIndex1] = newArray[randomIndex2] 
            newArray[randomIndex2] = extra 
            randomIndex1 = Math.floor(Math.random()*newArray.length)
            randomIndex2 = Math.floor(Math.random()*newArray.length)
        }
        return newArray.splice(0, no_of_word)
    }


    const keypressHandler = (e) =>{
        if(e.key === " " ){
            // for start 
            if(!typingTestData.isStarted && !typingTestData.isCompleted){
                InitializeHandler()
                console.log("hello")
                return 0;
            }
            //for restart
            console.log(!typingTestData.isStarted , typingTestData.isCompleted , readyToRestart)
            if(!typingTestData.isStarted && typingTestData.isCompleted && readyToRestart){
                InitializeHandler()
                console.log("hello, second")
                return 0;
            }
            if(!Time.isStarted){
                console.log("Timmer", Time)
                setTime({
                    time: 0,
                    isStarted: true
                })
                startTimmer();
            }
            pushWord()
            addNewWordFromPending()
        }
        if(e.key !== "Enter" && e.key !== " " && e.key !== "Delete" && typingTestData.isStarted && !typingTestData.isCompleted)
        {
            if(!Time.isStarted){
                setTime({
                    time: 0,
                    isStarted: true
                })
                startTimmer();
            }

            setCurrentWord(prev=>{
                if(prev.cursor < prev.word.length){
                    let newWord = prev.typed + e.key
                    let newCursor = prev.cursor + 1
                    return {
                        ...prev,
                        typed: newWord,
                        cursor: newCursor,
                        correct: getCorrectIncorrect(prev.word, newWord).correct,
                        error: getCorrectIncorrect(prev.word, newWord).error
                    }
                }else{
                    return {...prev}
                }
            })
        }
        
        if(e.key === "Delete"){
            setCurrentWord(prev=>{
                return {
                    ...prev,
                    typed: "",
                    cursor: 0,
                    correct: 0,
                    error: 0,
                }
            })
        }
    }

    const backspaceHandler = (e) =>{
        if(e.key === "Backspace"){
            //only for backspace
            setCurrentWord(prev=>{
                let newWord = prev.typed.slice(0, -1)
                let newCursor = (prev.cursor !== 0 ? prev.cursor - 1 : 0)
                return {
                    ...prev,
                    typed: newWord,
                    cursor: newCursor,
                    correct: getCorrectIncorrect(prev.word, newWord).correct,
                    error: getCorrectIncorrect(prev.word, newWord).error
                }
            })
        }
    }

    const InitializeHandler = () => {
        // setting timmer 
        setTime({
            isStarted: false,
            time: 0
        })

        //initializing data 
        setTypingTestData(prev=>({
            ...prev,
            isStarted: true,
            correctWord: 0,
            incorrectWord: 0,
            wpm: 0,
            cpm: 0,
            acc: 100,
            isCompleted: false,
        }))

        //feeding data to array
        setPendingArray(mixingWordArray(Word, 2000, 300)) //2000 shuffle, 300 word array return

        //setting first word
        setCurrentWord({
            word: mixingWordArray(Word, 2000, 1)[0],
            typed: "",
            cursor: 0,
            error: 0,
            correct: 0,
        })

        // empty typed word array
        setTypedWordArray([])

        setReadytoRestart(false)
    }

    const startTimmer = () => {
        let timmerId = setInterval(()=> {
            
            setTime(prev=>{
                let newTime = prev.time+0.5
                if(prev.time+0.5 >= typingTestData.time*60){
                    // console.log("Hello")
                    console.log("stopped")

                    setTypingTestData(prev=>({
                        ...prev,
                        isStarted: false,
                        isCompleted: true,
                    }))
                    //stop timmer
                    stopTimmer(timmerId)
                    //ready to restart in 1 sec
                    setTimeout(()=>{
                        setReadytoRestart(true)
                        console.log(typingTestData.isStarted, typingTestData.isCompleted)
                    }, 1000);
                }
                return {
                    ...prev,
                    isStarted: true,
                    time: newTime
                }
            })
        }, 500) //1sec
        setIntervalId(timmerId)
    }

    const stopTimmer = (timmerId) => {
        if(timmerId){
            clearInterval(timmerId )
        }
    }

    const updateTypingData = () => {
        // console.log(typedWordArray)

        let totalErrorLetter = 0;
        let totalCorrectLetter = 0

        let correctWord = 0;
        let incorrectWord = 0;

        //stored correct incorrect word
        // console.log(typedWordArray)
        typedWordArray.forEach((wordEle)=>{

            totalCorrectLetter += wordEle.correct
            totalErrorLetter += wordEle.error
            // console.log(wordEle)
            if(wordEle.correct < wordEle.word.length){
                incorrectWord++
            }else{
                correctWord++
            }
        })

        //current word correct incorrect
        totalCorrectLetter += currentWord.correct
        totalErrorLetter += currentWord.error

        let acc = 100;
        // if(totalCorrectLetter && totalErrorLetter) 
        if(totalCorrectLetter !== 0 )
        acc = (100*totalCorrectLetter / (totalCorrectLetter + totalErrorLetter)).toFixed(2)
        else 
        acc = 0
        let wpm = ((totalCorrectLetter*60)/(Time.time*4)).toFixed(2)
        let cpm = ((totalCorrectLetter*60)/(Time.time)).toFixed(2)
        // console.log(correctWord, incorrectWord)

        setTypingTestData(prev=>{
            return{
                ...prev,
                incorrectWord,
                correctWord,
                acc,
                wpm,
                cpm,

            }
        })
        
    }

    const getCorrectIncorrect = (real, test) => {
         let correct = 0
         let incorrect = 0
         for(var i=0; i<test.length; i++){
            if(real[i] === test[i])
                correct++;
            else
                incorrect++;
         }
         return {correct: correct, error: incorrect}
    }

    const pushWord = () => {
        setTypedWordArray(prev=>{
            return [...prev, {
                word: currentWord.word,
                error: currentWord.error,
                correct: currentWord.correct,
            }]
        })
    }

    const addNewWordFromPending = () => {
        let newWord = pendingArray.splice(0, 1)[0]
        setCurrentWord({
            word: newWord,
            typed: "",
            cursor: 0,
            error: 0,
            correct: 0,
        })
    }
    const changeTimeHandler = (time) => {
        if(intervalId)
        clearInterval(intervalId)

        console.log(time)
        setTypingTestData(prev=>({
            ...prev,
            time: time,
            isStarted: false,
            isCompleted: false,
        }))

        setTime({
            isStarted: false,
            time: 0,
        })
    }
    // console.log(pendingArray)
    return (
        <div className={classes.Typing}>
            <div className={classes.Head}>
                Typing Test
            </div>
            {typingTestData && <TimeB changeTimeHandler={changeTimeHandler} TimerLength={typingTestData.time}/>}
            <div className={classes.Main}>
                {typingTestData && <Timer time={Time.time} TimeLength={typingTestData.time}/>}
                {typingTestData && <Area ReadyToRestart={readyToRestart} TypingTestData={typingTestData} typedWordArray={typedWordArray} PendingArray={pendingArray} CurrentWord={currentWord}/>}
                {typingTestData && <Status TypingTestData={typingTestData}/>}
            </div>
        </div>
    )
}

export default Typing
