import React from 'react'

//classes 
import classes from "./Word.css"

const Word = (props) => {

    let style = {}
    let Background = ""
    let FontSize = ""
    let color = "#882c2c"
    let margin = "2px"
    if(props.typed){
        Background = "linear-gradient(to right, #E3A9A9, #E96A6A, #E3A9A9)"
        FontSize = "1.7rem"
        if(props.isCorrect){
            Background = "linear-gradient(to right, #86C4E3, #46ADE1, #86C4E3)"
            color = "#2c4d88"
        }
    }else if(props.tobetyped){
        Background = "#D4D4D4"
        FontSize = "1rem"
        color = "#999999"
        margin = "1px"
    }
    style = {
        background: Background,
        FontSize: FontSize,
        color: color,
        margin: margin,
    }


    return (
        <div className={classes.Word} style={style}>
            {props.word}
        </div>
    )
}

export default Word
