import React from 'react'


//classes
import classes from "./TimeB.css"

const TimeB = (props) => {
    return (
        <div className={classes.TimeB}>
            <Button isActive={props.TimerLength === 1} clickHandler={()=>props.changeTimeHandler(1)} timeLength={"1 min"}/>
            <Button isActive={props.TimerLength === 3} clickHandler={()=>props.changeTimeHandler(3)} timeLength={"3 min"}/>
            <Button isActive={props.TimerLength === 5} clickHandler={()=>props.changeTimeHandler(5)} timeLength={"5 min"}/>
        </div>
    )
}

const Button = (props) => {
    console.log(props.isActive)
    return (
        <div style={{backgroundColor: props.isActive ? "#48BDD9" : null, color: props.isActive ? "#fff" : null}}  onClick={props.clickHandler} className={classes.Button}>
            {props.timeLength}
        </div>
    )
}

export default TimeB
