import React, {useState, useEffect} from 'react'

//classes
import classes from "./Timer.css"


const Timer = (props) => {

    let style = {
        height: `${(props.time/(0.6*props.TimeLength))}%`
    }
    return (
        <div className={classes.Timer}>
            <div className={props.time>=60 ? (classes.Complete+" "+classes.Range): classes.Range}>
                <div className={classes.Flow} style={style}></div>
            </div>
        </div>
    )
}

export default Timer
