import React from 'react'

//classes
import classes from "./Char.css"

const Char = (props) => {

    let style = {}
    let color = "#E96A6A"
    let border = "none"
    if(props.isCorrect){
        if(props.isCorrect){
            color = "#46ADE1"
        }
    }else if(props.untouched){
        color = "#999999"
    }
    if(props.onCursor){
        border = `2px solid #707070`
    }
    style = {
        color: color,
        borderBottom: border,
        // 86C4E3
    }
    
    // console.log(props.char)
    return (
        <div style={style} className={classes.Char}> 
            {props.char}
        </div>
    )
}

export default Char
